

.sidebarChannel:hover > h4{
    color: white;
    background-color: #40464b;
}

.sidebarChannel > h4{
    display: flex;
    padding-left: 15px;
    align-items: center;
    background-color: #2f3135;
    color: gray;
    cursor: pointer;
}


.sidebarChannel__hash{
    font-size: 3.5vh;
    padding: 8px;
}

.sidebar__voice, .sidebar__profile{
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: gray;
    background-color: #2f3135;
    padding: 10px;
    border-top: 1px solid gray;
}


.sidebar__voiceIcons{
    display: flex;
    flex-direction: column;
}


.sidebar__voiceInfo{
    flex: 1;
    padding: 10px;

}

.sidebar__voiceInfo > h3{
    color: #4fb185;
}

.sidebar__voiceIcon{
    color: #4fb185;
}


.sidebar__voiceInfo > p{
    font-size: smaller;
}


.sidebar__voiceIcons > .MuiSvgIcon-root{
    padding: 10px;
}


.sidebar__profileIcons{
    display: flex;
    flex-direction: column;
}

.sidebar__profileIcons  > .MuiSvgIcon-root{
    padding: 10px;
}


.sidebar__profileInfo{
    flex: 1;
    padding: 10px;
}


.sidebar__profileInfo > h3{
    color: white;
}



.sidebar__profileInfo > p{
    font-size: smaller;
}
