

.sidebar{
    display: flex;
    flex-direction: column;
    flex:0.25;
    height: 100vh;
    background-color: #2f3135;
}


.sidebar__top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #2f3135;
    color: white;
    border-bottom: 3px solid #26262c;
}


.sidebar__channels{
    flex: 1;
}

.sidebar__addChannel{
    cursor: pointer;
}


.sidebar__addChannel:hover{
    color: white;
}

.sidebar__channelsHeader{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #2f3135;
    color: gray;
}


.sidebar__header{
    display: flex;
    align-items: center;
}


.sidebar__channelList{
    flex: 1;
}


.avatar__photo :hover{
    cursor: pointer;
}